<template>
  <div>
    <div v-for="item in items" :key="item.id" @click="select(item.id)">
      <div class="blank-2"></div>
      <table class="list">
        <tr>
          <td> </td>
          <td>{{ item.info }}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{ item.disease }}</td>
        </tr>
        <tr>
          <td>/</td>
          <td>{{ item.schedule }}</td>
        </tr>
      </table>
      <div class="blank-2"></div>
    </div>
    <div style="height:40px;"></div>

    <div>
      <v-layout column ml-4 mr-4>
        <v-layout row justify-space-between>
          <v-btn fab class="round_btn" flat solo>
            <v-icon>arrow_back_ios</v-icon>
          </v-btn>

          <v-avatar size="100px">
            <img src="@/assets/sample_avatar.jpg">
          </v-avatar>

          <v-btn fab class="round_btn" flat solo>
            <v-icon>arrow_forward_ios</v-icon>
          </v-btn>
        </v-layout>

        <p> </p>
        <p>[4.7/5]</p>
        <p>20   .    .</p>
        <!-- <p>{{item.name}} </p>
        <p>{{item.rating}}</p>
        <p>{{item.description}}</p>-->
      </v-layout>
    </div>

    <table class="memo">
      <tr>
        <td class="ddd"> </td>
        <td>2018 12 16</td>
      </tr>
      <tr>
        <td class="ddd">  </td>
        <td>      .</td>
      </tr>
      <tr>
        <td class="ddd">, </td>
        <td>      .      .</td>
      </tr>
      <tr>
        <td class="ddd"> </td>
        <td> 30mg +  15mg  3  .</td>
      </tr>
      <tr>
        <td class="ddd">/</td>
        <td> 146/89,  183.</td>
      </tr>
    </table>

    <v-layout mt-4 justify-center>
      <v-btn class="mainBtn" color="#1675fa" outline @click="pressNext">
        <div class="btnDiv">
          <p> </p>
        </div>
      </v-btn>
    </v-layout>
    <div class="blank-4"></div>
    <div class="blank-4"></div>
  </div>
</template>



<script>
export default {
  data() {
    return {
      items: [
        // .     .
        {
          id: "1",
          info: "(6)/",
          disease: " ",
          schedule: "12/16() 8:00PM~8:30PM"
        }
      ]
    };
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
    },
    pressNext() {
      this.navigateTo({ name: "landing" });
    },
    select(obj) {
      console.log(obj);
      this.navigateTo({ path: "/history/" + obj });
    }
  }
};
</script>

<style scoped>
.blank-4 {
  height: 40px;
}
.blank-2 {
  height: 10px;
}

img {
  max-width: 100%;
  object-fit: cover;
}
.list {
  height: 90px;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #444444;
}

td {
  height: 30px;
  font-size: 14px;
  padding: 5px;
}
.ddd {
  width: 80px;
}

.mainBtn {
  font-size: 17px;
  font-weight: bolder;

  padding-top: 10px;
  padding-left: 26px;
  padding-right: 26px;
  padding-bottom: 10px;

  height: 47px;
  width: 150px;
  border-radius: 13px;
  border-width: 2px;
}
.btnDiv {
  transform: translateX(5%);
}
</style>


