<template>
  <div>
    <v-layout column>
      <v-flex>
        <img src="@/assets/takeReservation/res_specific_guide.png">
      </v-flex>

      <table class="list">
        <tr>
          <td> </td>
          <td>{{ item.info }}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{ item.address }}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{ item.contact }}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{ item.symptom }}</td>
        </tr>
        <tr>
          <td>/</td>
          <td>{{ item.schedule }}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{ item.family_history }}</td>
        </tr>
        <tr>
          <td> </td>
          <td>{{ item.medication }}</td>
        </tr>
        <tr>
          <td>/</td>
          <td>{{ item.height_weight }}</td>
        </tr>
      </table>
      <div class="blank-4"></div>

      <v-flex>
        <v-btn color="blue" outline @click="pressHome"></v-btn>
      </v-flex>
    </v-layout>
  </div>
</template>



<script>
export default {
  data() {
    return {
      item: {
        id: "1",
        info: " (6)",
        address: "  203 506 ",
        contact: "01012341234",
        symptom: " ",
        family_history: "",
        medication: "",
        height_weight: "113cm/20kg",
        schedule: "12/16() 8:00PM~8:30PM"
      }
    };
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
    },
    pressHome() {
      this.navigateTo({ name: "landing" });
    }
  }
};
</script>

<style scoped>
.blank-4 {
  height: 40px;
}

img {
  max-width: 100%;
  object-fit: cover;
}

td {
  height: 30px;
  font-size: 15px;
  padding: 8px;
}
</style>


